.list {
  display: flex;
  /* flex-wrap: wrap; */
  list-style: none;
  padding: 10px 0;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  overflow: auto;
  /* justify-content: start; */
}

.list li {
  margin-right: 10px;
  padding: 15px;
  background-color: #9c85d2;
  border-radius: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  /* display: inline-block; */
  flex-shrink: 0;
}

.list li:hover {
  background-color: #fd7e14;
}

.list li a {
  text-decoration: none;
  color: white;
  font-size: 20px;
  font-weight: 500;
}

.list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  /* border-radius: 10px; */
  background-color: #f5f5f5;
}

.list::-webkit-scrollbar {
  width: 5px;
  background-color: #f5f5f5;
}

.list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #bcb5cc;
}
